  margin: -1px;
  overflow: hidden;
  padding: 0;
  position: absolute !important;
  width: 1px;
  word-wrap: normal !important;
}

.screen-reader-text:focus {
  background-color: #f1f1f1;
  border-radius: 3px;
  box-shadow: 0 0 2px 2px rgba(0, 0, 0, 0.6);
  clip: auto !important;
  clip-path: none;
  color: #21759b;
  display: block;
  font-size: 0.875rem;
  font-weight: 700;
  height: auto;
  left: 5px;
  line-height: normal;
  padding: 15px 23px 14px;
  text-decoration: none;
  top: 5px;
  width: auto;
  z-index: 100000;
}

body {
  font-family: "Nunito", Arial, Helvetica, Verdana, sans-serif;
  color: #ffffff;
  margin: 0;
}

h1, h2, h3, h4, h5, h6 {
  font-family: "Baloo 2", Arial, Helvetica, Verdana, sans-serif;
}

p {
  line-height: 1.5;
  font-size: 1.125rem;
}

html {
  scroll-behavior: smooth;
}

body {
  background-color: #060b1d;
  color: #ffffff;
}

:target:before {
  content: "";
  display: block;
  height: 2rem;
  margin: -2rem 0 0;
}

a,
a:visited {
  color: #f20039;
  font-weight: 600;
}

a:hover,
a:focus {
  color: #fb4a74;
}

a:focus {
  outline: 2px solid #fb4a74;
}

a:active {
  color: #ffffff;
  background-color: #f20039;
}

.banner-img {
  position: relative;
}
.banner-img img {
  width: 100vw;
  height: 300px;
  object-fit: cover;
}
.banner-img .banner-gradient {
  width: 100%;
  height: 300px;
  background-image: linear-gradient(to right, #060b1d, transparent);
  opacity: 0.8;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
}

button {
  color: #ffffff;
  background-color: #f20039;
  font-size: 1rem;
  font-weight: 600;
  text-transform: uppercase;
  padding: 0.75rem 1rem;
  border: none;
  border-radius: 0.5rem;
  display: inline-block;
}

button:hover,
button:focus {
  background-color: #fb4a74;
}

button.outline {
  background-color: transparent;
  outline: 0.25rem solid #f20039;
  outline-offset: -0.25rem;
}

button.outline:hover,
button.outline:focus {
  background-color: #f20039;
}

.carousel .carousel-slide {
  position: relative;
}
.carousel .carousel-slide .carousel-gradient {
  width: 100vw;
  height: 20rem;
  background-image: linear-gradient(to right, #060b1d, transparent);
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
}
.carousel .carousel-slide .carousel-img {
  height: 20rem;
  object-fit: cover;
  object-position: 50% 25%;
}
.carousel .carousel-slide .carousel-text {
  position: absolute;
  top: 3rem;
  left: 2.8rem;
  right: auto;
  width: 13rem;
  text-align: left;
  z-index: 200;
}
.carousel .carousel-slide .carousel-text h1 {
  font-size: 1.6em;
}
.carousel .carousel-slide .carousel-text .carousel-btn {
  padding: 0.5rem 0.8rem;
  font-size: 0.9em;
}
.carousel .carousel-slide .carousel-text .excerpt {
  display: none;
}
.carousel button.control-prev::after {
  content: "❮";
  padding-left: 0.3rem;
}
.carousel button.control-next::after {
  content: "❯";
  padding-left: 0.3rem;
}
.carousel button.control-prev.control-arrow,
.carousel button.control-next.control-arrow {
  opacity: 0.7;
}
.carousel button.control-prev.control-arrow::before {
  border-right: 0 solid transparent;
  display: none;
}
.carousel button.control-next.control-arrow::before {
  border-left: 0 solid transparent;
  display: none;
}
.carousel .control-dots {
  display: none;
}
@media (min-width: 40em) {
  .carousel .carousel-slide .carousel-gradient,
  .carousel .carousel-slide .carousel-img {
    height: 22rem;
  }
  .carousel .carousel-slide .carousel-text {
    top: 4rem;
    left: 5rem;
    width: 18rem;
  }
  .carousel .carousel-slide .carousel-text h1 {
    font-size: 1.7em;
  }
  .carousel .carousel-slide .carousel-text .carousel-btn {
    padding: 0.7rem 1rem;
  }
  .carousel button.control-prev::after {
    padding-left: 0.5rem;
  }
  .carousel button.control-next::after {
    padding-right: 0.5rem;
  }
}
@media (min-width: 48em) {
  .carousel .carousel-slide .carousel-gradient,
  .carousel .carousel-slide .carousel-img {
    height: 27rem;
  }
  .carousel .carousel-slide .carousel-text {
    top: 2rem;
    left: 5rem;
    width: 20rem;
  }
  .carousel .carousel-slide .carousel-text h1 {
    font-size: 1.9em;
  }
  .carousel .carousel-slide .carousel-text p {
    font-size: 1.1em;
  }
  .carousel .carousel-slide .carousel-text .carousel-btn {
    padding: 0.7rem 1.3rem;
    font-size: 1em;
  }
  .carousel .carousel-slide .carousel-text .excerpt {
    display: block;
  }
  .carousel button.control-prev::after {
    padding-left: 0.9rem;
    font-size: 2rem;
  }
  .carousel button.control-next::after {
    padding-right: 0.9rem;
    font-size: 2rem;
  }
}
@media (min-width: 80em) {
  .carousel .carousel-slide .carousel-gradient,
  .carousel .carousel-slide .carousel-img {
    height: 31rem;
  }
  .carousel .carousel-slide .carousel-text {
    top: 3.5rem;
    left: 7rem;
    width: 24rem;
  }
  .carousel .carousel-slide .carousel-text h1 {
    font-size: 2.1em;
  }
  .carousel .carousel-slide .carousel-text p {
    font-size: 1.2em;
  }
  .carousel .carousel-slide .carousel-text .carousel-btn {
    padding: 0.7rem 1.4rem;
    margin-top: 0.8rem;
  }
  .carousel ul.control-dots {
    display: block;
    bottom: 0.9rem;
  }
  .carousel ul.control-dots li.dot {
    width: 0.7rem;
    height: 0.7rem;
    box-shadow: none;
  }
  .carousel ul.control-dots li.dot.selected {
    opacity: 0.7;
  }
  .carousel button.control-prev::after {
    padding-left: 1rem;
    font-size: 2.3rem;
  }
  .carousel button.control-next::after {
    padding-right: 1rem;
    font-size: 2.3rem;
  }
}
@media (min-width: 1600px) {
  .carousel .carousel-slide .carousel-gradient {
    height: 35rem;
    top: 0;
    left: 0;
    right: 0;
  }
  .carousel .carousel-slide .carousel-img {
    height: 35rem;
    object-position: 50% 25%;
  }
  .carousel .carousel-slide .carousel-text {
    top: 3.6rem;
    left: 9rem;
    width: 28rem;
  }
  .carousel .carousel-slide .carousel-text h1 {
    font-size: 2.4em;
  }
  .carousel .carousel-slide .carousel-text p {
    font-size: 1.4em;
  }
  .carousel .carousel-slide .carousel-text .carousel-btn {
    padding: 0.8rem 1.6rem;
    margin-top: 0.8rem;
    font-size: 1.3em;
  }
  .carousel button.control-prev::after {
    padding-left: 1.5rem;
    font-size: 2.8rem;
  }
  .carousel button.control-next::after {
    padding-right: 1.5rem;
    font-size: 2.8rem;
  }
}
@media (min-width: 1800px) {
  .carousel .carousel-slide .carousel-gradient {
    height: 38rem;
    top: 0;
    left: 0;
    right: 0;
  }
  .carousel .carousel-slide .carousel-img {
    height: 38rem;
    object-position: 50% 25%;
  }
  .carousel .carousel-slide .carousel-text {
    top: 3.6rem;
    left: 9rem;
    width: 32rem;
  }
  .carousel .carousel-slide .carousel-text h1 {
    font-size: 2.6em;
  }
  .carousel .carousel-slide .carousel-text p {
    font-size: 1.5em;
  }
  .carousel .carousel-slide .carousel-text .carousel-btn {
    padding: 0.8rem 1.7rem;
    margin-top: 0.8rem;
    font-size: 1.3em;
  }
  .carousel button.control-prev::after {
    padding-left: 1.8rem;
    font-size: 3rem;
  }
  .carousel button.control-next::after {
    padding-right: 1.8rem;
    font-size: 3rem;
  }
  .carousel ul.control-dots {
    bottom: 0.95rem;
  }
  .carousel ul.control-dots li.dot {
    width: 0.91rem;
    height: 0.91rem;
  }
}

.currentCategory {
  background-color: transparent;
  text-transform: none;
  text-align: left;
  border-radius: 0;
  border-bottom: 0.125rem solid #232945;
  min-width: 10rem;
  padding: 0;
  margin: 1rem 1rem 0 1rem;
  display: flex;
}

button.currentCategory:hover,
button.currentCategory:focus {
  background-color: transparent;
}

.currentCategory h1 {
  margin: 0 1rem 0.25rem 0;
}

.dropdownContainer {
  display: none;
  background-color: #232945;
  padding: 0.5rem 0;
  margin: 0 1rem;
  text-align: right;
  z-index: 90;
  right: 0;
  top: 4rem;
}

.categoryMenu.toggled .dropdownContainer {
  display: flex;
  flex-direction: column;
}

.dropdownContainer button.outline {
  outline: none;
}

@media (min-width: 48em) {
  .currentCategory {
    display: none;
  }
  .categoryMenu.toggled .dropdownContainer {
    flex-direction: row;
  }
  .dropdownContainer {
    display: flex;
    background-color: #232945;
    padding: 1rem;
    margin: 1rem auto;
    display: block;
    width: max-content;
    border-radius: 0.5rem;
  }
  .dropdownContainer button {
    margin: 0 0.5rem;
    min-width: 10rem;
  }
  .dropdownContainer button.outline {
    outline: 0.25rem solid #f20039;
    outline-offset: -0.25rem;
  }
}
svg path {
  stroke-linecap: round;
  stroke-linejoin: round;
}

.site-logo {
  fill: #f20039;
  line-height: 0;
  padding: 0.875rem;
}

.site-logo:hover {
  color: #fb4a74;
  fill: #fb4a74;
}

.menu-toggle {
  display: inline-block;
  background-color: transparent;
  border: 0;
}

.menu-toggle svg path {
  stroke: #ffffff;
  stroke-width: 0.25rem;
  transform-origin: right center;
  transition: opacity 200ms ease, width 200ms ease, rotate 200ms ease, translate 200ms ease;
}

button.favorite {
  fill: #232945;
  stroke: #ffffff;
  stroke-width: 0.125rem;
  background-color: transparent;
  padding: 0;
  height: 3rem;
  width: 3rem;
}

button:hover .favorite {
  stroke: #fb4a74;
}

button.favorite.favorited {
  fill: #f20039;
  stroke: #f20039;
}

button.currentCategory svg path {
  fill: transparent;
  stroke: #ffffff;
  stroke-width: 0.25rem;
}

.search svg path {
  fill: transparent;
  stroke: #ffffff;
  stroke-width: 0.25rem;
}

.search button {
  background-color: transparent;
}

.search button svg {
  height: 2rem;
  width: 2rem;
}

button.search {
  padding: 0;
}

button:hover .favorite {
  stroke: #fb4a74;
}

button.favorite.favorited {
  fill: #f20039;
  stroke: #f20039;
}

.movie-card {
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: 1fr;
  justify-content: center;
  height: max-content;
  width: 11.5625rem;
  margin: 0.625rem;
}

.movie-card img {
  grid-row: 1/span 3;
  grid-column: 1;
  z-index: -1;
  width: 100%;
  border-radius: 10px;
  opacity: 1;
  background-color: #060b1d;
  transition: all 0.2s ease;
}

.movie-card .movie-info {
  opacity: 0;
  grid-row: 1;
  grid-column: 1;
  padding: 1rem;
}

.movie-card p,
.movie-card h3 {
  margin: 0;
}

.movie-info {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.movie-info p {
  font-size: 1rem;
  line-height: 1.25;
}

.movie-card .title-and-release .title {
  font-size: 1.125rem;
}
.movie-card .title-and-release .release-date {
  font-size: 0.875rem;
  text-transform: uppercase;
}

.card-icons {
  display: flex;
  justify-content: space-between;
}

.movie-card button.favourite {
  grid-row: 3;
  opacity: 1;
  transition: all 0.2s ease-in-out;
}

.movie-card:hover img {
  opacity: 0.2;
}
.movie-card:hover .movie-info,
.movie-card:hover .title-and-release,
.movie-card:hover .vote-average,
.movie-card:hover button.favorite {
  opacity: 1;
}

.movies-category {
  max-width: max-content;
  margin: 0 auto;
}

.movies-container {
  justify-content: center;
  display: flex;
  column-gap: 1rem;
  row-gap: 1rem;
  flex-wrap: wrap;
  max-width: 80em;
}

.movies-category h1 {
  display: none;
  text-align: center;
}

@media (min-width: 48em) {
  .movies-category h1 {
    display: block;
  }
}
.search label {
  display: none;
}

.search form {
  display: flex;
  align-items: center;
  justify-content: end;
  position: relative;
}

.search input {
  color: #ffffff;
  background-color: #060b1d;
  height: 2.875rem;
  width: 3rem;
  border: 0.125rem solid #ffffff;
  border-radius: 2rem;
  padding: 0.125rem 1rem;
  transition: all 0.5s ease-in-out;
  opacity: 0;
}

.search input:focus {
  outline: 0.125rem solid #f20039;
  outline-offset: -0.1875rem;
}

.search button {
  position: absolute;
}

.search svg path:hover {
  stroke: #f20039;
}

.search.toggled input {
  width: 12rem;
  opacity: 1;
}

.spinner {
  border: 4px solid rgba(0, 0, 0, 0.1);
  width: 40px;
  height: 40px;
  border-radius: 50%;
  border-left-color: #f20039;
  animation: spin 1s ease infinite;
  margin: 50vh auto;
}

@keyframes spin {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
header {
  position: relative;
}

.site-header {
  background-color: #232945;
  position: sticky;
  z-index: 100;
  top: 0;
  left: 0;
  right: 0;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

header > a {
  color: #ffffff;
  display: flex;
  align-items: center;
  text-decoration: none;
}

.site-logo p {
  font-family: "Baloo 2", Arial, Helvetica, Verdana, sans-serif;
  font-size: 1.375rem;
  font-weight: 700;
  display: none;
  padding: 0 0 0 1rem;
  margin: 0;
}

.site-logo:focus {
  outline: none;
}

@media (min-width: 48em) {
  .site-logo p {
    display: inline-block;
  }
}
.site-navigation.toggled .menu-toggle svg path:first-of-type {
  rotate: -45deg;
  translate: 0.1rem -0.21rem;
}

.site-navigation.toggled .menu-toggle svg path:last-of-type {
  rotate: 45deg;
  translate: 0.1rem 0.24rem;
}

.site-navigation.toggled .menu-toggle svg path:nth-of-type(2) {
  opacity: 0;
  width: 0;
}

.site-navigation ul {
  background-color: #232945;
  display: none;
  height: 0;
  list-style: none;
  padding: 0;
  margin: 0;
  text-align: right;
  position: absolute;
  width: 80%;
  z-index: 90;
  right: 0;
  top: 4rem;
}

.site-navigation.toggled ul {
  display: block;
  height: 100vh;
}

.site-navigation a {
  color: #ffffff;
  background-color: #232945;
  font-size: 1.25rem;
  font-weight: 700;
  text-decoration: none;
  text-transform: uppercase;
  padding: 1.25rem;
  display: block;
}

/* navigation links */
header nav a:hover,
header nav a:focus,
header nav a:active {
  background-color: #f20039;
}

header nav a:focus {
  outline: 0.125rem solid #f20039;
}

.nav-icons {
  display: flex;
  align-items: center;
}

@media (min-width: 48em) {
  nav a {
    padding: 1rem;
  }
  .menu-toggle {
    display: none;
  }
  .site-navigation {
    display: flex;
    justify-content: flex-end;
  }
  .site-navigation ul {
    display: flex;
    position: static;
    align-items: center;
    top: 0;
    height: 4rem;
    width: auto;
  }
  .site-navigation.toggled ul {
    display: flex;
    height: 3rem;
  }
  .site-navigation a {
    height: 4rem;
  }
}
