@use "../utilities" as *;

.tmdb-logo {
  width: 5rem;
  height: auto;
  margin: 0 auto;
  display: block;
}

.about-text{
  margin: 1rem ;
}

.about {
  img {
    object-position: 0 40%;
  }

  .about-heading {
    color: $pri;
  }

  // 768px
  @include mq($medium-mq) {
    .about-text {
      padding: 2rem 4rem;
    }
  }

  // 1024px
  @include mq($big-mq) {
    .about-text {
      padding: 2rem 7rem 3rem 7rem;
    }
  }


  // 1280px
  @include mq($large-mq) {
    .about-text {
      padding: 3rem 10rem 5.5rem 10rem;
    }
  }

  //1600px
  @include mq(1600px) {
    .about-text {
      padding: 3.5rem 14rem 6rem 14rem;
    }
  }

  //1800px
  @include mq(1800px) {
    .about-text {
      padding: 3.5rem 16rem 6rem 16rem;
    }
  }
}
